    height: 100%;
    font-family: 'Roboto';
}

.logo svg{
    color: white;
    height: 2.5rem;
    width: 2.5rem;
}

li{
    list-style-type: none;
}

header{
    padding: 2rem;
    background-color: #333333;
    color: white;
    display: flex;
    align-items: center;
    gap: 1rem;
}

.content{
    display: flex;
    min-height: calc(100vh - 140px);
}

.sidebar{
    padding: 1.25rem;
    width: 300px;
    background-color: #eaeaea;
}

.general-tasks{
    display: flex;
    flex-direction: column;
}

.task-btn{
    border: none;
    background-color: transparent;
    width: 100%;
    text-align: start;
    padding: 0.5rem;
    border-radius: 5px;
}
.task-btn:hover{
    background-color: #b2b2b2;
}

#add-project-name{
    width: 100%;
}

.btn-control{
    margin-top: 0.3rem;
    display: flex;
    width: 100%;
    gap: 0.5rem;
}

.btn-control button{
    text-align: center;
    border-radius: 5px;
    width: 100%;
    padding: 0.2rem;
    border: none;
    font-weight: bold;
}

.submit-project-btn{
    background-color: #4CAF50;    
}

.cancel-btn{
    background-color: #FF6F61;

}

.project-previews{
    width: 70%;
    margin: 0 auto;
    padding: 2rem 1rem;
}

.project-element{
    display: flex;
    align-items: center;
    gap: 0.5rem;
}



.add-project-tasks-btn{
    border: none;
    background-color: transparent;
    font-size: 1.5rem;
    margin-top: 0.5rem;
    text-align: left;
    padding: 0.5rem;
}

#add-project-form{
    padding: 0.5rem;
}

.add-project-task-form, .edit-project-task-form{
    margin-top: 0.5rem;
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    padding: 1rem 0.5rem;
    border-radius: 5px;
    background-color: #F4F4F4;
    box-shadow: 0 0 0.5rem 0 rgba(0, 0, 0, 0.5);
}

.add-project-task-form input{
    padding: 0.75rem;
}



.remove-project-btn, .project-btn{
    border: none;
    background-color: transparent;
    padding: 0.5rem;
}

.project-btn{
    flex: 1;
    text-align: start;

}

.project-preview-lists{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}

.task-submit, .edit-task-submit {
    border: none;
    background: transparent;
    width: 15%;
    padding: 0.5rem;
    background-color:#4CAF50;
    color: white;
    border-radius: 5px;
}

.cancel-task-btn{
    border: none;
    background: transparent;
    width: 15%;
    padding: 0.5rem;

    background-color:#FF6F61;
    color: white;
    border-radius: 5px;
}

.task-name{
    font-size: 1.5rem;
}

.task-preview{
    padding: 0.5rem 1rem;
    color: white;
    display: flex;
    border-radius: 5px;
    justify-content: space-between;
    align-items: center;
}

.project-header{
    margin-top: 1rem;
}

.task-control-btn{
    display: flex;
    gap: 0.5rem;
}

.task-control-btn button{
    border: none;
    padding: 1rem;
    flex: 1;
    background-color: transparent;
}

.task-details-pop-up, .edit-project-task-form{
    position: fixed;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    box-sizing: border-box;
    background-color: white;
    padding: 1rem;
    border-radius: 5px;
}

.overlay{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}


.more-details-btn svg, .remove-task-button svg, .task-edit-btn svg{
    color: white;
    height: 1.5rem;
    width: 1.5rem;
}


.add-task-control-btn, .edit-task-control-btn{
    display: flex;

    gap: 0.5rem;
}

.priority-low{
    background-color: #2196F3 ;
}

.priority-med{
    background-color: #FFC107;
}

.priority-high{
    background-color: #D32F2F   ;
}


.is-task-complete-btn{
    padding: 0.5rem;
    margin-top: 1rem                                                                                                                                                                                                                                                                            ;
    background-color:#FF6F61;
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 0.5rem 0 rgba(0, 0, 0, 0.3);
    font-weight: bold;
    width: 30%;
}

.remove-project-btn svg{
    width: 1.5rem;
    height: 1.5rem;
}

.completed{
    background-color:#4CAF50;
}

textarea{
    padding: 0.75rem;
    resize: none;
    height: 200px;
}

.hide{
    display: none;
}

.show{
    display: block;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAEA;IACI,YAAY;IACZ,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,cAAc;IACd,aAAa;AACjB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,+BAA+B;AACnC;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,YAAY;IACZ,6BAA6B;IAC7B,WAAW;IACX,iBAAiB;IACjB,eAAe;IACf,kBAAkB;AACtB;AACA;IACI,yBAAyB;AAC7B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,WAAW;IACX,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,kBAAkB;IAClB,WAAW;IACX,eAAe;IACf,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;;AAE7B;;AAEA;IACI,UAAU;IACV,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;AACf;;;;AAIA;IACI,YAAY;IACZ,6BAA6B;IAC7B,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,oBAAoB;IACpB,kBAAkB;IAClB,yBAAyB;IACzB,2CAA2C;AAC/C;;AAEA;IACI,gBAAgB;AACpB;;;;AAIA;IACI,YAAY;IACZ,6BAA6B;IAC7B,eAAe;AACnB;;AAEA;IACI,OAAO;IACP,iBAAiB;;AAErB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,uBAAuB;IACvB,UAAU;IACV,eAAe;IACf,wBAAwB;IACxB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,uBAAuB;IACvB,UAAU;IACV,eAAe;;IAEf,wBAAwB;IACxB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,OAAO;IACP,6BAA6B;AACjC;;AAEA;IACI,eAAe;IACf,UAAU;IACV,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,YAAY;IACZ,sBAAsB;IACtB,uBAAuB;IACvB,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,MAAM;IACN,WAAW;IACX,YAAY;IACZ,oCAAoC;AACxC;;;AAGA;IACI,YAAY;IACZ,cAAc;IACd,aAAa;AACjB;;;AAGA;IACI,aAAa;;IAEb,WAAW;AACf;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,4BAA4B;AAChC;;;AAGA;IACI,eAAe;IACf,4RAA4R;IAC5R,wBAAwB;IACxB,YAAY;IACZ,kBAAkB;IAClB,2CAA2C;IAC3C,iBAAiB;IACjB,UAAU;AACd;;AAEA;IACI,